module.exports = "<app-blog-post title=\"ARC 16, Studio RAP and Technological Innovation\" subtitle=\"\" [isSummary]=\"isSummary\">\n  <div class=\"summary\" *ngIf=\"isSummary\">\n    <p>Every company is a tech company. Although seemingly corny, this idea surfaced continuously in popular media throughout\n      2016. TechCrunch even went further to say that all companies are learning to be tech businesses today. The field of\n      architecture is no exception. Technological innovation continuously opens new forms of artistic and functional expression.</p>\n  </div>\n  <div class=\"body\" *ngIf=\"!isSummary\">\n    <p>\n      Every company is a tech company. Although seemingly corny, this idea surfaced continuously in popular media throughout 2016.\n      TechCrunch even went further to say that all companies are learning to be tech businesses today. The field of architecture\n      is no exception. Technological innovation continuously opens new forms of artistic and functional expression.\n    </p>\n    <p>\n      Studio RAP (Robotics, Architecture, Production) recently received the ARC 16 award for innovation in architecture. I’m glad\n      to have an opportunity to be a part of the trip that this young start-up is taking. In this article, I’ll try to shed\n      some light on what it means to innovate the business of architecture today and how this architectural office is growing\n      to become a tech company.\n    </p>\n\n    <figure>\n      <img src=\"/assets/blog-images/arc-innovation-award-tech-innovation/Skilledin office.jpg\" alt=\"Skilledin office\" width=\"100%\">\n      <figcaption>Skilledin office</figcaption>\n    </figure>\n\n    <figure>\n      <img src=\"/assets/blog-images/arc-innovation-award-tech-innovation/Skilledin office interior.jpg\" alt=\"Skilledin office interior\"\n        width=\"100%\">\n      <figcaption>Skilledin office interior</figcaption>\n    </figure>\n\n    <p>\n      So, why do architects collaborate with programmers? What does programming have to do with architecture? Why is it so fun\n      to code geometry? How all these things lead to innovation?\n    </p>\n\n    <p>\n      It’s no secret that computers changed the way in which architects’ design buildings. In the last decades drawing boards were\n      replaced by rows of monitors, Oculus Rift’s and HoloLenses, but the underlying economic process remained almost intact.\n      During the last centuries, though, a bigger transition was happening and architects went from being master builders\n      to draftsmen, having extensive knowledge of catalogues containing prefabricated building components from various manufacturers.\n      Even though assembling such “Lego” parts requires creativity and ingenuity, freedom of designer to go around standards\n      deteriorated or to put it differently - grew to be very expensive. Imagining new ways of building became very difficult.\n      Rules were set and everyone became accustomed to the separation of designing and building. Architects were making drawings,\n      engineers were solving the math and fabrication was left to companies that own heavy machinery. Increased segregation\n      made collaboration very difficult. Pressured by limited budgets and time, engineers were involved only at the end of\n      design process to make sure that buildings will stand. Architects lost understanding on how machines work and what\n      are the real possibilities of manufacturing. Producers became too big to listen to requirements of relatively small\n      architectural or engineering offices and went further to use old methods of manufacturing as it involved smaller risks.\n      Companies specialized and communication failed. The central economic position and craftsmanship of the master builder\n      was lost in multiple translations and communication problems. Standardization delivered efficiency in the short term\n      but led to detachment between design, manufacturing and engineering in the long run, negatively affecting the pace\n      of innovation in the entire building industry.\n    </p>\n\n    <figure>\n      <img src=\"/assets/blog-images/arc-innovation-award-tech-innovation/Innovation of presentation, not the industry.png\" alt=\"Innovation of presentation, not the industry\"\n        width=\"100%\">\n      <figcaption>Innovation of the presentation, not the industry</figcaption>\n    </figure>\n\n    <p>\n      Ecological, functional and performance demands of the present are pushing new businesses in the building industry to rethink\n      their position in the economy and to get out of the previously described loophole. The automotive industry suggests\n      an alternative way. Even though stagnating with its combustion engines, the communication lines between design and\n      manufacturing teams were kept short. In car producing companies, bottom up iterative design processes were developed.\n      They were based on close collaboration between all those involved. Manufacturing was pushed to use automation in new\n      and innovative ways. And although industry was using machines to make millions of “standard” cars, they succeeded in\n      developing general purpose reprogrammable robotic arms which, by design and on demand, could do different tasks continuously.\n      To architects, such general-purpose machinery opens up huge possibilities.\n    </p>\n\n    <p>\n      Studio RAP is one of the first offices to implement the automotive’s model of integrated design and fabrication in an architectural\n      company. The goal is to take back control by resurrecting idea of the master builder. Positioning a new generation\n      of computational architects, programmers and engineers together with robots at the epicentre of the building process\n      could remove those failing communication protocols described previously. It means though, that such a start-up needs\n      to do everything itself - design, engineer and manufacture. This, of course, is no simple task…\n    </p>\n\n    <figure>\n      <img src=\"/assets/blog-images/arc-innovation-award-tech-innovation/Aggregate assembler.jpg\" alt=\"Aggregate assembler\" width=\"50%\">\n      <figcaption>Aggregate assembler</figcaption>\n    </figure>\n\n    <p>\n      In order to achieve all this, one has to understand the computational nature of geometry, physical properties of materials,\n      intricacies, possibilities and limitations of manufacturing machines and more. CAD (Computer Aided Design) software,\n      used by almost all architectural offices nowadays allows designers to make virtual geometries, but these programs,\n      designed through expert system methodologies are quite limiting. Developed 3D models are mostly used to produce visualizations\n      of virtual environments for clients and to derive architectural drawings for builders. But computers are able to do\n      much more than that. New generation of CAD software opens up its API’s (Application Programming Interfaces) to more\n      advanced use cases. Algorithmic thinking becomes crucial there. These entry points liberate users from the restrictions\n      of enclosed interface possibilities and allow new kinds of creative parametric scenarios that can integrate materiality\n      and production process into one synchronized design environment.\n    </p>\n\n    <figure>\n      <img src=\"/assets/blog-images/arc-innovation-award-tech-innovation/Back to the core.png\" alt=\"Back to the core\" width=\"100%\">\n      <figcaption>Back to the core</figcaption>\n    </figure>\n\n    <p>\n      Furthermore, manufacturing machines are driven by programs. Robots do not understand designer’s intent, section blueprints\n      or even CAD geometry until someone feeds them code, which represents movement instructions. The mentioned API’s become\n      crucial when one wants to do non-standard things with their software packages and open communication streams from CAD\n      software to robot controllers. Through programming Studio RAP is able to translate the design intent to the language\n      that the machines can understand. From the beginning of design, computational architects and programmers are aware\n      of the processes that will be involved in manufacturing and assembly of building components. The team prepares projects\n      in a way so that computed geometries can be directly processed into robot movement instructions. This requires close\n      and immediate collaboration between many specialists. And programmers are very welcome in this picture. Such work involves\n      deep understanding of procedural solvers that take structures of geometry as an input and compute various optimized\n      paths for machines to cut. This work is fun and emotionally rewarding as code is being outputted in a form of produced\n      geometry or a building. Bugs also acquire a very different meaning here. But let’s leave this theme for some other\n      time.\n    </p>\n\n    <figure>\n      <img src=\"/assets/blog-images/arc-innovation-award-tech-innovation/Assembling Skilledin Office.jpg\" alt=\"Assembling Skilledin Office\"\n        width=\"100%\">\n      <figcaption>Assembling Skilledin Office</figcaption>\n    </figure>\n\n    <p>The ARC16 Innovation Award was awarded to Studio RAP for their Skilledin office in the RDM Innovation Dock in Rotterdam.\n      This parametrically designed object embodies ideas I talked about in this article. The jury found the direction appealing\n      and I couldn’t agree more. Studio RAP took a big risk in making something so complex and by realizing it within the\n      budget constraints of the client as their first project. The team used refurbished second hand robots and experimented\n      a lot to design all those unique panels that fit together perfectly in forming an optimized vault structure. The journey\n      did not end there of course. The studio is developing software that drives industrial robots. It recently released\n      the first version of RAPCAM (Computer Aided Manufacturing) software that helps other designers to get started using\n      robots more easily. Through software, the studio transforms the research into more abstract and reusable workflows\n      and general algorithms. You can find more information on http://rapcam.eu. Studio RAP currently experiments with many\n      different techniques of manufacturing. This will allow the office to create even more amazing projects. Being probably\n      the first architectural office having programmers in the team, Studio RAP is moving fast to become a tech company.\n      I am very happy to witness such a transition and to have the opportunity to collaborate with this young start-up. I\n      hope that bringing professional knowledge of software development to the field of architecture will give a positive\n      effect to our built environment.\n    </p>\n    \n    <p>To conclude, I hope I managed to explain what it means to innovate in architectural sector nowadays. If you want to learn\n      more about this, you can find me on twitter as\n      <span><a href=\"https://twitter.com/ubarevicius\" target=\"_blank\" title=\"Twitter Matas_U\">@ubarevicius</a></span>. Also, check\n      out the videos afterwards to get more insight on what we do and visit http://studiorap.nl or follow Studio RAP on <span><a href=\"https://www.facebook.com/studiorapnl\" target=\"_blank\" title=\"Studio RAP NL\">Facebook</a></span>      or\n      <span><a href=\"https://www.instagram.com/studio.rap/\" target=\"_blank\" title=\"Studio RAP\">Instagram</a></span> for updates!\n    </p>\n\n    <iframe width=\"100%\" height=\"350\" src=\"https://www.youtube.com/embed/jN96XHXC1AA\" frameborder=\"0\" allowfullscreen></iframe>\n    <iframe width=\"100%\" height=\"350\" src=\"https://www.youtube.com/embed/RM7xvrm2Gc4\" frameborder=\"0\" allowfullscreen></iframe>\n\n  </div>\n</app-blog-post>"

